import { utm } from '@/api/backend/futures/others';

export default (qs) => {
  if (!qs.utm_source || !qs.utm_medium || !qs.utm_campaign) {
    return Promise.resolve(null);
  }

  return utm({
    utmSource: qs.utm_source,
    utmMedium: qs.utm_medium,
    utmCampaign: qs.utm_campaign,
  });
};



